<template>
  <view class="query-expense-container">
    <u-form :model="form" ref="uForm">
      <u-form-item label="开始日期">
        <u-datetime-picker
          v-model="form.startDate"
          mode="date"
          :max-date="new Date().getTime()"
        ></u-datetime-picker>
      </u-form-item>
      <u-form-item label="结束日期">
        <u-datetime-picker
          v-model="form.endDate"
          mode="date"
          :max-date="new Date().getTime()"
        ></u-datetime-picker>
      </u-form-item>
      <u-form-item label="状态">
        <!-- <u-select v-model="form.status" :list="statusOptions"></u-select> -->
      </u-form-item>
    </u-form>
    <u-button @click="handleSearch" type="primary">查询</u-button>

    <view class="expense-list" v-if="expenses.length > 0">
      <view
        class="expense-item"
        v-for="(expense, index) in expenses"
        :key="index"
      >
        <text>日期：{{ formatDate(expense.date) }}</text>
        <text>类型：{{ expense.type }}</text>
        <text>金额：{{ expense.amount }}</text>
        <text>状态：{{ getStatusText(expense.status) }}</text>
      </view>
    </view>
    <view v-else>暂无数据</view>

    <u-loadmore :status="loadMoreStatus" @loadmore="loadMore" />
  </view>
</template>

<script>
export default {
  data() {
    return {
      form: {
        startDate: new Date(
          new Date().setDate(new Date().getDate() - 30)
        ).getTime(), // 默认查询最近30天
        endDate: new Date().getTime(),
        status: "",
      },
      statusOptions: [
        { label: "全部", value: "" },
        { label: "待审核", value: "pending" },
        { label: "已通过", value: "approved" },
        { label: "已拒绝", value: "rejected" },
      ],
      expenses: [],
      page: 1,
      pageSize: 20,
      loadMoreStatus: "loadmore",
      userInfo: null,
    };
  },
  onLoad() {
    this.userInfo = uni.getStorageSync("userInfo");
    if (!this.userInfo) {
      uni.redirectTo({ url: "/pages/login/login" });
      return;
    }
    this.handleSearch();
  },
  methods: {
    formatDate(timestamp) {
      const date = new Date(timestamp);
      return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(
        2,
        "0"
      )}-${String(date.getDate()).padStart(2, "0")}`;
    },
    getStatusText(status) {
      const statusMap = {
        pending: "待审核",
        approved: "已通过",
        rejected: "已拒绝",
      };
      return statusMap[status] || status;
    },
    handleSearch() {
      this.page = 1;
      this.expenses = [];
      this.loadMoreStatus = "loadmore";
      this.fetchExpenses();
    },
    fetchExpenses() {
      uniCloud
        .callFunction({
          name: "queryExpenses",
          data: {
            userId: this.userInfo._id,
            startDate: this.form.startDate,
            endDate: this.form.endDate,
            status: this.form.status,
            page: this.page,
            pageSize: this.pageSize,
          },
        })
        .then(res => {
          if (res.result.code === 0) {
            this.expenses = [...this.expenses, ...res.result.data];
            if (res.result.data.length < this.pageSize) {
              this.loadMoreStatus = "nomore";
            } else {
              this.loadMoreStatus = "loadmore";
            }
          } else {
            uni.showToast({
              title: res.result.message,
              icon: "none",
            });
          }
        })
        .catch(err => {
          uni.showToast({
            title: "查询失败，请重试",
            icon: "none",
          });
        });
    },
    loadMore() {
      if (this.loadMoreStatus === "loadmore") {
        this.page++;
        this.fetchExpenses();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.query-expense-container {
  padding: 20px;
}
.expense-list {
  margin-top: 20px;
}
.expense-item {
  background-color: #f8f8f8;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}
</style>
